@import url("https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap");

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: "Comfortaa", cursive;
	color: rgb(97, 218, 251);
	outline: none !important;
}
*:focus,
*:active {
	outline: none !important;
}
body {
	background-color: #282c34;
}
.button {
	background: none;
	border: 1px solid rgb(97, 218, 251);
	margin: 5px 0;
	outline: none;
    padding: 2vw 4vw;
	border-radius: 5px;
	// margin-right: 10px;
	cursor: pointer;
}
.button:hover {
	background: rgba(97, 218, 251, 0.1);
}
.input {
	outline: none;
	background: none;
	border: none;
	width: 100%;
	text-align: left;
	display: block;
}
.input:focus {
	outline: none !important;
}
.input::-moz-focus-inner {
	border: 0px !important;
}
.App {
	width: 100%;
	height: 100%;
    display: grid;
    grid-template-columns: 1fr;
}
.timer{
    display: block;
    margin: 30px auto 0;
    width: fit-content;
    &__paragraph{
        font-size: 3.5vw;
    }
}
.lostFigures{
    display: none;
}

.board {
    max-width: 512px;
    max-height: 512px;
	width: 80vw;
	height: 80vw;
    display: grid;
    grid-template-columns: repeat(8,1fr);
    grid-template-rows: repeat(8,1fr);
    margin: 0 auto;
    &__header{
        text-align: center;
        padding: 10px;
    }
}
.cell {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}
.cell img {
	width: 75%;
	height: 75%;
	position: relative;
}

.black {
	background-color: rgb(112, 112, 112);
}
.white {
	background-color: #fff;
}

.selected {
	background-color: brown;
}
.avaiable {
	height: 12px;
	width: 12px;
	border-radius: 50%;
	background-color: rgb(0, 192, 26);
}
.lost {
    height: calc(50% - 15px);
    width: calc(100% - 20px);
	background-color: rgb(211, 211, 211);
    border-radius: 5px;
    // margin: 5px 5px 0 5px;
    margin-top: 10px;
    margin-bottom: 10px;
	color: black;
    &:last-child{
        margin-top: 0px;
        margin-bottom: 10px;
    }
    &__list{
        display: grid;
        grid-template-columns: 1fr;
        gap: 5px;
        width: fit-content;
        scrollbar-width: none;
        overflow-y: scroll;
        height: calc(100% - 80px);
        padding: 10px;
        margin: 0 auto;
        &::-webkit-scrollbar {
            display: none;
          }
    }
    &__title{
        height: 70px;
        text-align: center;
        padding: 10px;
        font-size: 20px;
        align-self: center;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}
.lost h3 {
	color: black;
}
.lostItem {
	color: black;
}
@media (min-width: 640px) {
	.App {
		width: 100vw;
		height: 80vh;
        grid-template-columns: 2fr 1fr;
        max-width: 950px;
        margin: 0 auto;
	}
    .timer{
        margin: 30px auto 0;
        &__paragraph{
            font-size: 18px;
        }
    }
    .lostFigures{
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        max-width: 300px;
    }
    .button {
        padding: 10px 20px;
    }
    .board {
        width: calc(64px * 8);
        height: calc(64px * 8);
        display: flex;
        flex-wrap: wrap;
    }
    .cell {
        width: 64px;
        height: 64px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .cell img {
        width: 48px;
        height: 48px;
        position: relative;
    }
}
@media (min-width: 850px){
    .lost {
        height: calc(50% - 15px);
        width: calc(100% - 20px);
        background-color: lightgray;
        // margin: 5px 5px 0 5px;
        margin-top: 10px;
        margin-bottom: 10px;
        color: black;
        &:last-child{
            margin-top: 0px;
            margin-bottom: 10px;
        }
        &__list{
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 10px;
            width: fit-content;
            overflow-y: scroll;
            height: calc(100% - 80px);
            padding: 10px;
            margin: 0 auto;
        }
        &__title{
            height: 70px;
            text-align: center;
            padding: 10px;
            font-size: 20px;
            align-self: center;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
}